// ** React Imports
import { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** Vendor Edit Components
import AccountTab from './Account'
import VendorEmployeeTab from '../../VendorEmployee/List/VendorEmployee'

// ** Store & Actions
import { getVendor } from '@store/Vendors/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import { Info, User } from 'react-feather'
import { Alert, Card, CardBody, Col, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap'

// ** Custom Components
import UILoader from '@components/ui-loader'

// ** Styles
import '@styles/react/apps/app-users.scss'

const VendorEdit = () => {
  // ** States & Vars
  const [activeTab, setActiveTab] = useState('1'),
    dispatch = useDispatch(),
    { isLoading, selectedVendor } = useSelector(state => state.vendors),
    { id } = useParams()

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

  // ** Function to get vendor on mount
  useEffect(() => {
    dispatch(getVendor(id))
  }, [dispatch])

  return selectedVendor !== null && selectedVendor !== undefined ? (
    <UILoader blocking={isLoading}>
      <Row className='app-user-edit'>
        <Col sm='12'>
          <Card>
            <CardBody className='pt-2'>
              <Nav pills>
                <NavItem>
                  <NavLink active={activeTab === '1'} onClick={() => toggle('1')}>
                    <User size={14} />
                    <span className='align-middle d-none d-sm-block'>Account</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={activeTab === '2'} onClick={() => toggle('2')}>
                    <Info size={14} />
                    <span className='align-middle d-none d-sm-block'>Employee</span>
                  </NavLink>
                </NavItem>
              </Nav>
              <TabContent activeTab={activeTab}>
                <TabPane tabId='1'>
                  <AccountTab selectedVendor={selectedVendor} />
                </TabPane>
                <TabPane tabId='2'>
                  <VendorEmployeeTab />
                </TabPane>
              </TabContent>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>Vendor not found</h4>
      <div className='alert-body'>
        Vendor with id: {id} doesn't exist. Check list of all Vendors: <Link to='/vendors/list'>Vendors List</Link>
      </div>
    </Alert>
  )
}

export default VendorEdit
