// ** React Imports
import { useState } from 'react'
import { useDispatch, useSelector } from "react-redux"
import classnames from "classnames"

// ** Custom Components
import UserEditAvatar from '@components/user-avatar/UserEditAvatar'
import PermissionsTable from '@components/table/PermissionsTable'
import InputGroup from '@components/input-group'
import UILoader from '@components/ui-loader'

// ** Third Party Components
import { Button, Col, Form, Row } from 'reactstrap'

// ** Store Actions
import { updateVendor, vendorEditChange } from '@store/Vendors/actions'
// ** Utils UploadFile
import uploadFile from '@utils/uploadFile'

const VendorAccountTab = () => {
  // ** States
  const [imgLoading, setImgLoading] = useState(false)

  const dispatch = useDispatch(),
    { formErrors, isLoading, selectedVendor } = useSelector(state => state.vendors)
  const { rolesConfig } = useSelector(state => state.roles)
  const handleFormChange = (name, value) => {
    dispatch(vendorEditChange(name, value))
  }

  // ** Function to change vendor image
  const onChange = async e => {
    const files = e.target.files
    setImgLoading(true)
    try {
      const url = await uploadFile(files[0])
      handleFormChange('profilePictureURL', url)
      setImgLoading(false)
    } catch (e) {
      setImgLoading(false)
    }
  }

  const { entityType, status } = rolesConfig.vendor
  const entityTypeOptions = Object.keys(entityType).map((mKey) => {
    return (
      <option key={mKey} value={mKey}>{entityType[mKey]}</option>
    )
  })

  const statusOptions = Object.keys(status).map((sKey) => {
    return (
      <option key={sKey} value={sKey}>{status[sKey]}</option>
    )
  })

  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(updateVendor(selectedVendor._id))
  }

  return (
    <Row>
      <Col sm='12'>
        <UserEditAvatar
          imgLoading={imgLoading}
          name={selectedVendor.vendorName}
          photo={selectedVendor.profilePictureURL}
          onChangeAvatar={onChange}
        />
      </Col>
      <Col sm='12'>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Vendor Name'}
                error={formErrors['vendorName']}
                name={'vendorName'}
                placeholder={'Vendor Name'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendor.vendorName}
                required={true}
                className={classnames({ 'is-invalid': formErrors['vendorName'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Owner Name'}
                error={formErrors['ownerName']}
                name={'ownerName'}
                placeholder={'Owner Name'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendor.ownerName}
                required={true}
                className={classnames({ 'is-invalid': formErrors['ownerName'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Email'}
                error={formErrors['email']}
                name={'email'}
                placeholder={'Email'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendor.email}
                required={true}
                className={classnames({ 'is-invalid': formErrors['email'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'select'}
                label={'Country Code'}
                error={formErrors['countryCode']}
                name={'countryCode'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendor.countryCode}
                required={true}
                className={classnames({ 'is-invalid': formErrors['countryCode'] })}
              >
                <option value=''>Select Country Code</option>
                <option value='254'>+254 Kenya</option>
              </InputGroup>
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'number'}
                label={'Phone Number'}
                error={formErrors['phoneNumber']}
                name={'phoneNumber'}
                placeholder={'Phone Number'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendor.phoneNumber}
                required={true}
                className={classnames({ 'is-invalid': formErrors['phoneNumber'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Website'}
                error={formErrors['websiteURL']}
                name={'websiteURL'}
                placeholder={'https://'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendor.websiteURL}
                required={true}
                className={classnames({ 'is-invalid': formErrors['websiteURL'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'select'}
                label={'Entity Type'}
                error={formErrors['entityType']}
                name={'entityType'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendor.entityType}
                required={true}
                className={classnames({ 'is-invalid': formErrors['entityType'] })}
              >
                <option value=''>Select Entity Type</option>
                {entityTypeOptions}
              </InputGroup>
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'select'}
                label={'Status'}
                error={formErrors['status']}
                name={'status'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedVendor.status}
                className={classnames({ 'is-invalid': formErrors['status'] })}
              >
                <option value=''>Select Status</option>
                {statusOptions}
              </InputGroup>
            </Col>
            <Col sm='12'>
              <PermissionsTable />
            </Col>
            <Col className='d-flex flex-sm-row flex-column mt-2' sm='12'>
              <UILoader blocking={isLoading} className='mr-sm-1'>
                <Button.Ripple className='mb-1 mb-sm-0 mr-0' type='submit' color='primary'>
                  Save Changes
                </Button.Ripple>
              </UILoader>
              <Button.Ripple color='secondary' outline>
                Reset
              </Button.Ripple>
            </Col>
          </Row>
        </Form>
      </Col>
    </Row>
  )
}

export default VendorAccountTab
